import React, { Component } from 'react'
import {

    CButton,

    CCardBody,

    CCardHeader,
    CCol,

    CRow,

} from '@coreui/react'

import CIcon from '@coreui/icons-react'
// import "grapesjs/src/styles/scss/main.scss";
import 'grapesjs/dist/css/grapes.min.css';

import './Edit.css'

import grapesjs from 'grapesjs';
import 'grapesjs-preset-webpage';
import 'grapesjs-blocks-flexbox';
// import 'grapesjs-blocks-flexbox-center';
import 'grapesjs-blocks-yang-basic';
// import 'grapesjs-plugin-forms';
import 'grapesjs-lory-slider';


// import { Editor } from 'grapesjs-react';


import Axios from '../../../lib/api';
import swal from 'sweetalert';

const REACT_APP_BASE_URL = require('../../../lib/env').default;
class Edit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            title: '',
            description: '',
            keywords: '',
            html: '',
            url: '',
            loading: false
        }
        console.log(props)
        this.pageId = props.match.params.id;

        console.log(this.pageId)
    }
    initGrapes() {
        this.editor = grapesjs.init({
            // forceClass: false,
            // Indicate where to init the editor. You can also pass an HTMLElement
            container: '#gjs',
            // Get the content for the canvas directly from the element
            // As an alternative we could use: `components: '<h1>Hello World Component!</h1>'`,
            fromElement: true,
            showOffsets: 1,
            // Size of the editor
            // height: '300px',
            width: 'auto',
            noticeOnUnload: false,
            storageManager: {
                id: 'gjs-',             // Prefix identifier that will be used inside storing and loading
                type: 'local',          // Type of the storage
                autosave: false,         // Store data automatically
                autoload: false,         // Autoload stored data on init
                stepsBeforeSave: 0,     // If autosave enabled, indicates how many changes are necessary before store method is triggered
                storeComponents: true,  // Enable/Disable storing of components in JSON format
                storeStyles: false,      // Enable/Disable storing of rules in JSON format
                storeHtml: false,        // Enable/Disable storing of components as HTML string
                storeCss: false,         // Enable/Disable storing of rules as CSS string
            },
            blockManager: {
                // appendOnClick: true,
                blocks: []
            },
            styleManager: {
                sectors: [
                    {
                        name: 'General',
                        open: false,
                        buildProps: ['float', 'display', 'position', 'top', 'right', 'left', 'bottom']
                    },
                    {
                        name: 'Dimension',
                        open: false,
                        buildProps: ['width', 'flex-width', 'height', 'max-width', 'min-height', 'margin', 'padding'],
                        properties: [{
                            id: 'flex-width',
                            type: 'integer',
                            name: 'Width',
                            units: ['px', '%'],
                            property: 'flex-basis',
                            toRequire: 1,
                        }]
                    },
                    {
                        name: 'Decorations',
                        open: false,
                        buildProps: ['color', 'border-radius-c', 'background-color', 'border-radius', 'border', 'box-shadow', 'background'],
                    },
                    {
                        name: 'Typography',
                        open: false,
                        buildProps: ['color',],
                    },
                ],
            },
            assetManager: {
                assets: [],
                upload: `${REACT_APP_BASE_URL}storage/upload`,        //for temporary storage
                uploadFile: (e) => {
                    var files = e.dataTransfer ? e.dataTransfer.files : e.target.files;
                    var formData = new FormData();
                    formData.append("file", files[0]);
                    Axios.post(`${REACT_APP_BASE_URL}storage/upload`, formData, {
                        headers: {
                            'Content-Type': 'multipart/form-data'
                        }
                    }).then(r => {
                        const am = this.editor.AssetManager;
                        am.add(r.data);
                    }).catch(e => { console.error(e) })
                }
            },
            mediaCondition: 'min-width', // default is `max-width`
            plugins: [
                'gjs-blocks-flexbox',
                // 'gjs-blocks-flexbox-center',
                // 'grapesjs-blocks-yang-basic',
                'grapesjs-plugin-forms',
                // 'grapesjs-lory-slider',
                // 'grapesjs-preset-webpage'
                'gjs-preset-webpage'
            ],
            cssComposer: {

            },
            pluginsOpts: {
                // 'grapesjs-lory-slider': {
                //     sliderBlock: { label: 'Slider' }
                // },
                'grapesjs-blocks-yang-basic': {
                    actionUrl: `${REACT_APP_BASE_URL}contract_record/add-contract-record`,
                },

                'gjs-blocks-flexbox': {},
                'rapesjs-blocks-flexbox-center': {},

                'grapesjs-plugin-forms': {
                    blocks: ['input', 'textarea', 'select', 'button', 'label',],//'form', 
                    actionUrl: `${REACT_APP_BASE_URL}contract_record/add-contract-record`,
                },
                'gjs-preset-webpage2': {
                },
                'gjs-preset-webpage': {
                    // formsOpts: {
                    //     blocks: ['input', 'textarea', 'select', 'label',]// 'button'
                    // },
                    formsOpts: false,

                    countdownOpts: false,
                    navbarOpts: false,
                    modalImportTitle: 'Import Template',
                    modalImportLabel: '<div style="margin-bottom: 10px; font-size: 13px;">Paste here your HTML/CSS and click Import</div>',
                    modalImportContent: function (editor) {
                        return editor.getHtml() + '<style>' + editor.getCss() + '</style>'
                    },
                    filestackOpts: null, //{ key: 'AYmqZc2e8RLGLE7TGkX3Hz' },
                    // aviaryOpts: {},

                    blocksBasicOpts1: { flexGrid: 1 },
                    blocksBasicOpts: {
                        blocks: ['column1', 'column2', 'column3', 'text', 'link', 'image']//, 'video', 'map' ,'column3-7',
                    },

                },
            },
            canvas: {
                styles: [
                    // 'https://stackpath.bootstrapcdn.com/bootstrap/4.1.3/css/bootstrap.min.css'
                    'https://rawcdn.githack.com/rafaelbotazini/floating-whatsapp/3d18b26d5c7d430a1ab0b664f8ca6b69014aed68/floating-wpp.min.css',

                ],
                // scripts: [
                //   'https://code.jquery.com/jquery-3.3.1.slim.min.js',
                //   'https://cdnjs.cloudflare.com/ajax/libs/popper.js/1.14.3/umd/popper.min.js',
                //   'https://stackpath.bootstrapcdn.com/bootstrap/4.1.3/js/bootstrap.min.js'
                // ],
            }
        });

        this.editor.DomComponents.addType('input', {
            isComponent: el => el.tagName === 'INPUT',
            model: {
                defaults: {
                    traits: [
                        // Strings are automatically converted to text types
                        'name', // Same as: { type: 'text', name: 'name' }
                        'placeholder',
                        {
                            type: 'select', // Type of the trait
                            label: 'Type', // The label you will see in Settings
                            name: 'type', // The name of the attribute/property to use on component
                            options: [
                                { id: 'text', name: 'Text' },
                                { id: 'email', name: 'Email' },
                                { id: 'password', name: 'Password' },
                                { id: 'number', name: 'Number' },
                            ]
                        }, {
                            type: 'checkbox',
                            name: 'required',
                        }],
                    // As by default, traits are binded to attributes, so to define
                    // their initial value we can use attributes
                    attributes: { type: 'text', required: true },
                },
            },
        });

        this.editor.BlockManager.add('my-contract-block', {
            label: `
            <svg class="gjs-block-svg" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
              <path class="gjs-block-svg-path" d="M22,5.5 C22,5.2 21.5,5 20.75,5 L3.25,5 C2.5,5 2,5.2 2,5.5 L2,8.5 C2,8.8 2.5,9 3.25,9 L20.75,9 C21.5,9 22,8.8 22,8.5 L22,5.5 Z M21,8 L3,8 L3,6 L21,6 L21,8 Z" fill-rule="nonzero"></path>
              <path class="gjs-block-svg-path" d="M22,10.5 C22,10.2 21.5,10 20.75,10 L3.25,10 C2.5,10 2,10.2 2,10.5 L2,13.5 C2,13.8 2.5,14 3.25,14 L20.75,14 C21.5,14 22,13.8 22,13.5 L22,10.5 Z M21,13 L3,13 L3,11 L21,11 L21,13 Z" fill-rule="nonzero"></path>
              <rect class="gjs-block-svg-path" x="2" y="15" width="10" height="3" rx="0.5"></rect>
            </svg>
            <div class="gjs-block-label">Contract Form</div>`,
            content: `
            <form method="POST" action="${REACT_APP_BASE_URL}contract_record/add-contract-record" >
            <div style="background:#F2CA03;padding:20px;margin:auto;max-width: 350px;margin-top:10px;">
          <div class="form-group">
            
            <input placeholder="Type here your name" name="name" style="width:100%;text-align:center;"/>
          </div>
          <div class="form-group">
            <input type="email" placeholder="Type here your email" name="email" style="width:100%;text-align:center;margin-top:10px;"/>
          </div>
          </div>
          <div class="form-group">
            <button type="submit" style="display:block;width:100%;">Send</button>
          </div>
          
        </form>
            `,
        });
        this.editor.BlockManager.add('my-whatsapp-block', {
            label: `
            <svg xmlns="http://www.w3.org/2000/svg" width="40" height="50" viewBox="0 0 24 24"><path d="M.057 24l1.687-6.163c-1.041-1.804-1.588-3.849-1.587-5.946.003-6.556 5.338-11.891 11.893-11.891 3.181.001 6.167 1.24 8.413 3.488 2.245 2.248 3.481 5.236 3.48 8.414-.003 6.557-5.338 11.892-11.893 11.892-1.99-.001-3.951-.5-5.688-1.448l-6.305 1.654zm6.597-3.807c1.676.995 3.276 1.591 5.392 1.592 5.448 0 9.886-4.434 9.889-9.885.002-5.462-4.415-9.89-9.881-9.892-5.452 0-9.887 4.434-9.889 9.884-.001 2.225.651 3.891 1.746 5.634l-.999 3.648 3.742-.981zm11.387-5.464c-.074-.124-.272-.198-.57-.347-.297-.149-1.758-.868-2.031-.967-.272-.099-.47-.149-.669.149-.198.297-.768.967-.941 1.165-.173.198-.347.223-.644.074-.297-.149-1.255-.462-2.39-1.475-.883-.788-1.48-1.761-1.653-2.059-.173-.297-.018-.458.13-.606.134-.133.297-.347.446-.521.151-.172.2-.296.3-.495.099-.198.05-.372-.025-.521-.075-.148-.669-1.611-.916-2.206-.242-.579-.487-.501-.669-.51l-.57-.01c-.198 0-.52.074-.792.372s-1.04 1.016-1.04 2.479 1.065 2.876 1.213 3.074c.149.198 2.095 3.2 5.076 4.487.709.306 1.263.489 1.694.626.712.226 1.36.194 1.872.118.571-.085 1.758-.719 2.006-1.413.248-.695.248-1.29.173-1.414z"/></svg>
            <div class="gjs-block-label">Whats app</div>`,
            content: `
<div style="text-align: center;">
            <div style="margin:auto;width: auto;display: inline-block;">
                <a href="tel:073-7958888" style="text-decoration: unset;">
                    <div
                        style="display: flex;background: #F2CA03;height: 77px;padding-left: 10px;padding-right:10px;border-radius: 35px;">
                        <div
                            style="align-self: center;flex-basis: 50px;margin-right: 10px;width: 50px;height: 50px;background-size: 100%;background-repeat: no-repeat;background-image: url(http://masters-lps.com/api/storage/storage/1606332534603_Untitled-4%20copy.PNG);">
                        </div>
                        <div style="align-self: center;color:#000;font-size: 30px;font-family: fantasy;font-weight:bold;">
                            073-7958888</div>
                    </div>
                </a>
            </div>
        </div>`,
        });

        this.editor.BlockManager.add('image', {
            label: 'Images',
            // category: c.category,
            attributes: { class: 'gjs-fonts gjs-f-image' },
            content: {
                style: { color: 'black' },
                type: 'image',
                activeOnRender: 1
            },
        });

        this.editor.BlockManager.add('text', {
            label: 'Text',
            // category: c.category,
            attributes: { class: 'gjs-fonts gjs-f-text' },
            content: {
                type: 'text',
                content: 'Insert your text here',
                style: { padding: '10px' },
                activeOnRender: 1
            },
        });

        this.editor.BlockManager.add('link', {
            label: 'Link',
            // category: c.category,
            attributes: { class: 'fa fa-link' },
            content: {
                type: 'link',
                content: 'Link',
                style: { color: '#d983a6' }
            },
        });



        // this.editor.BlockManager.add('my-first-block', {
        //     label: 'Blank Block',
        //     content: `<div  draggable="true class="blank-block" style="width: 100%;
        //     min-height:100px;
        //     display: block;
        //     background: gray;
        //     margin:auto;
        //     max-width: 900px;">Blank</div>`,
        // });

        // this.editor.BlockManager.add('bootstrap-block', {
        //     label: 'Bootstrap',
        //     content: `<link rel="stylesheet" href="https://stackpath.bootstrapcdn.com/bootstrap/4.5.2/css/bootstrap.min.css" integrity="sha384-JcKb8q3iqJ61gNV9KGb8thSsNjpSL0n8PARn9HuZOnIxN0hoP+VmmDGMN5t9UJ0Z" crossorigin="anonymous">`,
        // });
        // Commands
        this.editor.Commands.add('set-device-desktop', {
            run: editor => editor.setDevice('Desktop')
        });
        this.editor.Commands.add('set-device-mobile', {
            run: editor => editor.setDevice('Mobile')
        });
        this.editor.Commands.add('set-device-tablet', {
            run: editor => editor.setDevice('Tablet')
        });
        // Do something on response
        this.editor.on('asset:upload:response', (response) => {
            console.log(response)
            const am = this.editor.AssetManager;
            am.add(response);
        });
        const cssComposer = this.editor.CssComposer;

        // cssComposer.setRule('.input', {
        //     width: '100%',
        //     height: '54px',
        //     padding: '0px 8px',
        //     'margin-bottom': '12px',
        //     border: 'solid 1px #000000',
        //     'text-align': 'center',
        //     color: '#000',
        //     'font-weight': 'bold',
        //     'font-size': '19px',
        //     'border-radius': '0px'
        // });

        // cssComposer.setRule('.textarea', {
        //     width: '100%',
        //     height: '54px',
        //     padding: '0px 8px',
        //     'margin-bottom': '12px',
        //     border: 'solid 1px #000000',
        //     'text-align': 'center',
        //     color: '#000',
        //     'font-weight': 'bold',
        //     'font-size': '19px',
        //     'border-radius': '0px'
        // });

        cssComposer.setRule('img', {
            width: '100%',
            display: 'block',
            margin: 'auto',
            'max-width': '900px'
        });
        cssComposer.setRule('.blank-block', {
            width: '100%',
            'min-height': '100px',
            display: 'block',
            background: 'gray',
            // margin: 'auto',
            'max-width': '900px'
        });

        Axios.get('storage/storage-list').then(r => {
            const am = this.editor.AssetManager;
            am.add(r.data);
        })
    }
    componentDidMount = () => {

        if (this.pageId && parseInt(this.pageId) > 0)
            Axios.get(`admin/page/pages?id=${this.pageId}`).then(r => {
                console.log(r.data)
                this.setState({ url: r.data[0].url, page: r.data[0], title: r.data[0].title, description: r.data[0].description, keywords: r.data[0].keywords, html: r.data[0].html }, (() => {
                    this.externel_links = [];
                    var page = r.data[0];
                    if (page && page.externel_links) {
                        if (typeof page.externel_links == 'string') page.externel_links = JSON.parse(page.externel_links);
                        this.externel_links = page.externel_links;
                        console.error(this.externel_links)
                    }

                    this.initGrapes();
                    this.editor.setStyle(r.data[0].css)
                }))

                // this.editor.setComponents(r.data[0].html)


            })
        else {
            this.initGrapes();
        }
    }
    valueChange(e, column) {
        this.setState({ [column]: e.target.value })
    }
    _save() {
        this.setState({ loading: true })
        var { title, page, description, keywords, url } = this.state;

        Axios.post('admin/page/add-page', { id: page ? page.id : null, html: this.editor.getHtml(), css: this.editor.getCss(), title: title, description: description, keywords: keywords, lead_id: this.lead_id, url: url, externel_links: this.externel_links })
            .then(r => {
                console.log(r)
                this.props.history.push('/')
            })
            .catch(e => console.error(e))

    }
    onSave() {
        var { page, url } = this.state;

        if (this.state.url === null || this.state.url.length === 0) {
            swal({
                title: "Input page url",
                // text: "Check your email",
                icon: "error",
                buttons: {
                    confirm: {
                        text: "OK",
                        value: true,
                        visible: true,
                        className: "",
                        closeModal: true
                    },
                }
            });
            return
        }

        Axios.post('admin/page/check-page-url', { id: page ? page.id : null, url: url })
            .then(r => {
                if (r.data.data == null) return this._save();

                swal({
                    title: "Please input other url",
                    // text: "Check your email",
                    icon: "error",
                    buttons: {
                        confirm: {
                            text: "OK",
                            value: true,
                            visible: true,
                            className: "",
                            closeModal: true
                        },
                    }
                });
            })
    }

    onTitleChange(e) {
        this.setState({ title: e.target.value })
    }
    onKeywordsChange(e) {
        this.setState({ keywords: e.target.value })
    }
    onDescriptionChange(e) {
        this.setState({ description: e.target.value })
    }
    render() {

        return (
            <>
                <CRow>
                    <CCol>
                        {/* <CCard> */}
                        <CCardHeader>
                            Add New Page
                            </CCardHeader>
                        <CCardBody>
                            <div className="row">
                                <div className='col-12 pb-2'>
                                    <input type="text" value={this.state.url} className="input form-control" placeholder="Page Url" onChange={(e) => this.valueChange(e, 'url')} />
                                </div>
                                <div className='col-3'>
                                    <input type="text" value={this.state.title} className="input form-control" placeholder="Title" onChange={(e) => this.onTitleChange(e)} />
                                </div>
                                <div className='col-3'>
                                    <input type="text" value={this.state.keywords} className="input form-control" placeholder="Keywords" onChange={(e) => this.onKeywordsChange(e)} />
                                </div>
                                <div className='col-3'>
                                    <input type="text" value={this.state.description} className="input form-control" placeholder="Description" onChange={(e) => this.onDescriptionChange(e)} />
                                </div>
                                <div className='col-3 text-right'>
                                    <CButton type="submit" size="sm" color="primary" onClick={() => { this.onSave() }}><CIcon name="cil-scrubber" /> Save
                                    {this.state.loading && <span className="spinner-border spinner-border-sm ml-1" role="status" aria-hidden="true"></span>}
                                    </CButton>
                                </div>
                            </div>



                            {/* <Editor /> */}
                            <div id="gjs" className="mt-3">
                                <div dangerouslySetInnerHTML={{ __html: this.state.html }}></div>
                            </div>

                        </CCardBody>
                        {/* </CCard> */}
                    </CCol>
                </CRow>
            </>
        )
    }
}

export default Edit
